body {background-color: #222629; color: #eee; font-weight:bold;}

.inputconfig {
    display: inline-block;
}
.controldummyfill {
    flex-grow: 1;
}
.itemContainer .controlcontainer {
    margin-top: -0.75em;
    display: inline-flex;
    flex-direction: column;
    margin-bottom: 0.75em;
    position: relative;
}
.controladd,
.controlnew,
.controldelete,
.controlup,
.controldown,
.controldummy {
    padding: 0px 0.4em;
    font-family: 'Courier New', monospace;
    font-weight: bold;
    cursor: pointer;
    height: 1.5em;
    border-radius: 6px;
    width: fit-content;
}
.controladd:hover,
.controlnew:hover,
.controldelete:hover,
.controlup:hover,
.controldown:hover {
    background-color: #666;
}
.controlnew::after {
    content: "+ New";
}
.configContainer .controlnew::after{
    content: "+";
}
.controladd::after {
    content: "+";
}
.controldelete::after {
    content: "-";
}
.controlup::after {
    content: "";
}
.controldown::after {
    content: "";
}
.controldummy::after {
    content: "\\00a0";
}
.subOperationSelection {
    width: 110px;
}
.subOperationName {
    width: 125px;
}
.itemContainer .item .configContainer {
    min-height: 4em;
}
.itemContainer .item{
    min-width: 850px;
    width: fit-content;
    box-sizing: border-box;
}
.configContainer {
    vertical-align:top; 
    border-radius: 6px;
    border-style: solid;
    border-width: 1px;
    border-color: #666;
    padding: 2.5px;
    margin: 0px;
    min-width: 850px;
    width: fit-content;
    box-sizing: border-box;
}
.itemList {
    padding-top: 0.75em;
}
.configContainer .itemContainer .item,
.configContainer .configContainer {
    min-width: auto;
    width: 100%
}
.configContainer .itemContainer {
    min-width: auto;
    width: 100%
}
.itemContainer .item {
    margin-top: 0.5em;
    margin-bottom: 0.5em;
    border-radius: 6px;
    border-style: solid;
    border-width: 1px;
    border-color: #666;
}
.itemContainer:last-child .item {
    margin-bottom: 1.5em;
}
.mathContainer {
    vertical-align:top; 
    border-radius: 6px;
    border-style: solid;
    border-width: 1px;
    border-color: #666;
    padding: 2.5px;
    margin: 2.5px;
    display: inline-block;
}
.inputSpacer {
    height: 25px;
}
.ui.number.canMultiplier,
.ui.number.canAdder {
    width: 5em;
}
.ui.number.canBitOffset,
.ui.number.canBitLength {
    width: 3em;
}
.ui.text.genericCalculationName:disabled {
    display: none;
}
label:has(.ui.text.genericCalculationName:disabled)::before {
    content: attr(label);
}
.ui.text.canParseDataName,
.ui.text.genericCalculationName,
.ui.text.inputName {
    border: 0;
    outline: 0;
    background: transparent;
    border-radius: 0px;
    border-bottom: solid #3590d0;
    width: 250px;
}
.ui.text.canParseDataName {
    width: 150px;
}
label:not(.w3-button) {
    font: bold 16px Arial;
    display: inline-block;
    min-width: 256px;
    padding-right: 5px;
    margin: 1px;
}
label.w3-button,
input.w3-button {
    font: bold 16px Arial;
    border-radius: 6px;
    margin: 1px;
}
.button {
    font: bold 16px Arial;
    text-decoration: none;
    background-color: #505962;
    color: #dfdfdf;
    border-radius: 6px;
    border: 2px solid #1A1A20;
    cursor: pointer;
}
select {
    font: bold 16px Arial;
    text-decoration: none;
    background-color: #505962;
    color: #dfdfdf;
    border-radius: 6px;
    border: 2px solid #1A1A20;
    margin: 1px;
    padding: 1px 2px;
    display: inline-block;
}
.button:hovver,
select:hover {
    background-color: #666;
    border-color: #3590d0;
}

#overlay {
    position: fixed; /* Sit on top of the page content */
    display: none; /* Hidden by default */
    width: 100%; /* Full width (cover the whole page) */
    height: 100%; /* Full height (cover the whole page) */
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0); /* Black background with opacity */
    z-index: 200; /* Specify a stack order in case you're using a different order for other elements */
  }


select.gpiooverlayselect {
    position: absolute; 
    margin: 0;
    margin: 0;
    padding: 0;
    width: 150px;
}
select .pinconflict{
    background-color:rgb(255, 0, 0);
}
.pinconflict{
    background-color:rgb(255, 128, 128);
}
.livevalue {
    font-family: 'Courier New', monospace;
    color: #55AA55;
}

.context-menu {
    cursor: default;
    position: absolute;
    background: #222629;
    border: 1px solid #505962;
    min-width: 200px;
    max-height: 400px;
    overflow-y: auto;
    white-space: normal;
    inline-size: 200px;
    z-index: 10;
}

.context-menu ul {
    padding: 0px;
    margin: 0px;
    min-width: 150px;
    list-style: none;
}

.context-menu ul li {
    padding: 3px;
    border: 1px solid black;
}

.monospace {
    font-family: 'Courier New', monospace;
}

::-webkit-scrollbar {
    width: 1em;
}
::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}
::-webkit-scrollbar-thumb {
    background-color: #505962;
    outline: 1px solid #1A1A20;
    border-radius: 0.4em;
}

.pinoverlay {
    position: relative; 
    transform-origin: top left;
}
.pinoverlay img {
    position: absolute; left: 150px;
}

.sidebaropenclose::after {
    content: ""
}
.input-tab .expand::before {
    font-family:monospace;
    content: "\\00a0";
}
.input-tab .despand::before {
    font-family:monospace;
    content: "\\00a0";
}
.can-tab .expand::before {
    font-family:monospace;
    content: "\\00a0";
}
.can-tab .despand::before {
    font-family:monospace;
    content: "\\00a0";
}
.dashboard-tab::before,
.pinoverlay-tab::before,
.engine-tab::before,
.fuel-tab::before,
.ignition-tab::before {
    font-family:monospace;
    content: "\\00a0\\00a0";
}
.dashboard-tab::after {
    content: "Dashboard";
}
.pinoverlay-tab::after {
    content: "Pin Mapping";
}
.input-tab::after {
    content: "Inputs";
}
.can-tab::after {
    content: "CAN";
}
.engine-tab::after {
    content: "Engine";
}
.fuel-tab::after {
    content: "Fuel";
}
.ignition-tab::after {
    content: "Ignition";
}
.ui.text.formula {
    width: 100%;
    flex-grow: 1;
}

.ui.select.inputmeasurement .ui.selected {
    min-width: 120px;
}

.gauges {
    display: flex;
    flex-wrap: wrap;
}

.loggedVariables {
    border-right: 0.1em solid #eee
}
.loggedVariables table {
    outline: none;
    border-spacing: 0;
}
.loggedVariables table tr.selected td {
    background-color: #3590d0;
}
.loggedVariables table tr.selected td.actions {
    background-color: inherit;
}
.loggedVariables table td {
    border: 0.1em solid rgba(0, 0, 0, 0.3);
    border-spacing: 0;
    min-width: 50px;
    white-space: nowrap;
}
.loggedVariables table td.actions {
    border-right: 0;
    border-top: 0;
    border-bottom: 0;
    border-spacing: 0;
    min-width: 50px;
}
.loggedVariables table td.loggedVariableName {
    min-width: 200px;
}
.ui.select.refreshRateSelection .ui.selected {
    min-width: 50px;
}

.ui.button.loggedVariablesExpand {
    writing-mode: vertical-lr;
    height: fit-content;
}

.ui.context-menu.openned {
    position: inherit;
    width: 500px;
}

.ui.select.logged-variable-selection .ui.selected {
    width: 100%;
}
.ui.select.logged-variable-selection .ui.selected,
.ui.select.unit .ui.selected {
    min-width: 0px;
    background-color: transparent;
    color: var(--text-color);
    border: none;
    min-width: 2em;
}
.ui.select.logged-variable-selection .ui.selected::after {
    float: right;
}
.ui.select.unit .ui.selected::after {
    float: right;
    content: "";
}

.ui.text.gaugeTemplate {
    width: 100%;
    height: 25em;
}

.ui.select.can-bus-selection .ui.selected {
    min-width: 2.5em;
}
.ui.select.can-bus-selection .ui.context-menu {
    min-width: 2.5em;
    width: fit-content;
}

.ui.select.can-write-interval .ui.selected {
    min-width: 80px;
}
.ui.select.can-write-interval .ui.context-menu {
    min-width: 80px;
    width: fit-content;
